<template>
  <div v-if="modal == 2" class="modal-cover">
    <div class="modal-content">
      <div class="card white inline-block">
       <!-- <h2 class="blue-text">Tu Crédito autorizado de 24 a 48 hrs</h2> -->
        <img :src="require('@/assets/img/icons/share.svg')" class="modal-icon" />
        <div class="tabs">
            <div class="tab selected" style="padding-top:5px !important">
                <h3 style="color:#000 !important">COMPARTIR</h3>
            </div>
        </div>

        <mu-text-field v-model="name" label="Nombre" label-float></mu-text-field>
        <mu-text-field v-model="phone" type="tel" label="Celular" label-float></mu-text-field>
        <mu-text-field v-model="email" type="email" label="Correo Electrónico" label-float></mu-text-field>
        <mu-text-field v-model="message" label="Mensaje" label-float></mu-text-field>
        <br />
        <br />
        <div class="button-large" :class="{'disabled':disabled}">Correo Electrónico</div>
        <div v-if="disabled" class="button-large" :class="{'disabled':disabled}">WhatsApp</div>
        <a :href="`https://api.whatsapp.com/send?phone=${whatsappData.phone}&text=${whatsappData.message}`" target="_blank" v-else><div  class="button-large" :class="{'disabled':disabled}">WhatsApp</div></a>
        <div @click="updateModal(0)" class="button-large flat" >Volver</div>
      </div>
    </div>
  </div>
</template>
<script>
import { mapState, mapMutations } from "vuex";
export default {
  data() {
    return {
      name: "",
      phone: "",
      email: "",
      message: "",
    };
  },
  computed: {
    ...mapState(["modal"]),
    disabled() {
      if (this.name && this.phone && this.email && this.message) {
        return false;
      } else {
        return true;
      }
    },
    whatsappData(){
        return {
          phone : "52" + this.phone,
          message:""
        }
    }
  },
  methods:{
    ...mapMutations(["updateModal"])
  }
};
</script>